import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state = ({
            serialNumber : props.serialNumber,
            x: props.x,
            y: props.y,
        });
    }

    clickHandle(e){
        let newArray = [];

        for(let i = 0;i < 15;i++){
            newArray[i] = [];
            for(let j = 0;j < 32;j++)
                newArray[i][j] = 0;
        }

        this.props.mapActions.initMap(newArray);
    }

    render(){
        return(
            <div className="cell" onClick={this.clickHandle.bind(this,`${this.state.x} ${this.state.y}`)}>
                {this.state.serialNumber}
            </div>
        );
    }
}